@mixin windowMax2048p{
    @media (min-width: 1921px){
        @content;
    }
}

@mixin windowMax1920p{
    @media (min-width: 1701px) and (max-width: 1920px){
        @content;
    }
}

@mixin windowMax1440p{
    @media (min-width: 953px) and (max-width: 1700px){
        @content;
    }
}

