 @mixin windowMax1280p{
     @media (min-width: 953px) and (max-width: 1280px){
         @content;
     }
}

@mixin mobileMax952p{
    @media (min-width: 0px) and (max-width: 952px){
        @content;
    }
}


@mixin tablet{
    @media (min-width: 648px) {
        @content;
    }
}